<template>
  <v-container class="mt-4">
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="primary" dark @click="$router.push('/custom/registration_view?path=/itsf&role=' + queries.id + '&practitioner=' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Registration</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-alert type="primary" v-if="fullname">
      <center><b>Editing Registration For {{ fullname }}</b></center>
    </v-alert>
    <v-form id="app" ref="form" v-model="valid">
      <v-card
        class="mx-auto"
        max-width="700"
        outlined
      >
        <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
          Registration Information
        </v-card-title>
        <v-card-text class="my-3" v-if="!overlay">
          <fhir-choice field="unit" :initial="unit" :slotProps="{ source: { path: 'unit', data: [] }}" :edit="true" path="unit" displayCondition="" id="PractitionerRole.extension:maahad-unit.value[x]:valueCoding" definition="http://ihris.org/fhir/StructureDefinition/itsf-practitioner-role#PractitionerRole.extension:maahad-unit.value[x]:valueCoding" label="Unit" binding="http://ihris.org/fhir/ValueSet/itsf-maahad-unit-valueset" min="1" max="1"></fhir-choice>
          <fhir-choice field="stream" :initial="stream" :slotProps="{ source: { path: 'stream', data: [] }}" :edit="true" path="stream" displayCondition="" id="PractitionerRole.extension:maahad-stream.value[x]:valueCoding" definition="http://ihris.org/fhir/StructureDefinition/itsf-practitioner-role#PractitionerRole.extension:maahad-stream.value[x]:valueCoding" label="Stream" binding="http://ihris.org/fhir/ValueSet/itsf-maahad-stream-valueset" min="1" max="1"></fhir-choice>
          <fhir-choice field="accomodation" :initial="accomodation" :slotProps="{ source: { path: 'accomodation', data: [] }}" :edit="true" path="accomodation" displayCondition="" id="PractitionerRole.extension:acommodation.value[x]:valueCoding" definition="http://ihris.org/fhir/StructureDefinition/itsf-practitioner-role#PractitionerRole.extension:acommodation.value[x]:valueCoding" label="Accomodation" binding="http://ihris.org/fhir/ValueSet/itsf-accomodation-valueset" min="1" max="1"></fhir-choice>
          <fhir-choice field="student_type" :initial="student_type" :slotProps="{ source: { path: 'student_type', data: [] }}" :edit="true" path="student_type" displayCondition="" id="PractitionerRole.extension:student-type.value[x]:valueCoding" definition="http://ihris.org/fhir/StructureDefinition/itsf-practitioner-role#PractitionerRole.extension:student-type.value[x]:valueCoding" label="Student Category" binding="http://ihris.org/fhir/ValueSet/itsf-student-type-valueset" min="1" max="1"></fhir-choice>
          <fhir-reference field="school_fees_type" :initial="school_fees_type" :slotProps="{ source: { path: 'school_fees_type', data: [] }}" :edit="true" path="school_fees_type" displayCondition="" search-parameter="feestype" id="PractitionerRole.extension:school-fees-type.value[x]:valueReference" definition="http://ihris.org/fhir/StructureDefinition/itsf-practitioner-role#PractitionerRole.extension:school-fees-type.value[x]:valueReference" targetProfile="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-types" targetResource="Basic" label="Fees Category" displayType="preloaded" min="1" max="1"></fhir-reference>
          <fhir-choice field="family_head_category" :initial="family_head_category" :slotProps="{ source: { path: 'family_head_category', data: [] }}" :edit="true" path="family_head_category" displayCondition="school_fees_type|=|Basic/familyfive+=school_fees_type|=|Basic/familynine+=school_fees_type|=|Basic/familyinfinity" enableBehavior="any" id="PractitionerRole.extension:family-head-category.value[x]:valueCoding" definition="http://ihris.org/fhir/StructureDefinition/itsf-practitioner-role#PractitionerRole.extension:family-head-category.value[x]:valueCoding" label="Family Head Category" binding="http://ihris.org/fhir/ValueSet/itsf-family-head-category-valueset" min="1" max="1"></fhir-choice>
          <fhir-reference field="family_head" :initial="family_head" :slotProps="{ source: { path: 'family_head', data: [] }}" :edit="true" path="family_head" id="Basic.extension:head.value[x]:valueReference" definition="http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile#Basic.extension:head.value[x]:valueReference" report="ihris-es-itsf-family-head" reportReturnValue="ihris-es-itsf-family-head.id" referenceDisplayPath="name.where(use='official').given.first() + ' ' + name.where(use='official').family" label="Family Head" displayType="reportSelect" min="0" max="1"></fhir-reference>
        </v-card-text>
      </v-card>
      <v-card
        class="mx-auto"
        max-width="700"
        outlined
      >
        <v-card-actions>
          <v-row>
            <v-col>
              <v-btn class="error" dark @click="$router.push('/custom/registration_view?path=/itsf&role=' + queries.id + '&practitioner=' + queries.practitioner)">
                <v-icon light left>mdi-cancel</v-icon>
                <span>{{ $t("App.hardcoded-texts.Cancel") }}</span>
              </v-btn>
            </v-col>
            <v-spacer></v-spacer>
            <v-col>
              <v-btn
                v-if="valid"
                :disabled="!valid"
                class="success darken-1"
                dark
                @click="save()"
              >
                <v-icon light left>mdi-content-save</v-icon>
                <span>{{ $t("App.hardcoded-texts.Save") }}</span>
              </v-btn>
              <v-btn v-else class="green" dark @click="$refs.form.validate()">
                <v-icon light left>mdi-content-save</v-icon>
                <span>{{ $t("App.hardcoded-texts.Save") }}</span>
              </v-btn>
            </v-col>
          </v-row>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-container>
</template>

<script>
export default {
  props: ["queries"],
  data: function() {
    return {
      overlay: false,
      valid: true,
      amount: "",
      pay_method: "",
      pay_ref_num: "",
      unit: {},
      stream: {},
      accomodation: {},
      student_type: "",
      school_fees_type: "",
      family_head_category: "",
      family_head: "",
      family_head_resource: {},
      registration_resource: {},
      fullname: ""
    }
  },
  methods: {
    save() {
      this.overlay = true;
      this.populateValues(this.$children)
      let index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-unit"
      })
      this.registration_resource.extension[index].valueCoding = this.unit
      index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-stream"
      })
      this.registration_resource.extension[index].valueCoding = this.stream
      index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-acommodation"
      })
      this.registration_resource.extension[index].valueCoding = this.accomodation
      
      index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-student-type"
      })
      this.registration_resource.extension[index].valueCoding = this.student_type
      index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-school-fees-type"
      })
      this.registration_resource.extension[index].valueReference.reference = this.school_fees_type.reference
      index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-status"
      })
      if(index == -1) {
        this.registration_resource.extension.push({
          url: "http://ihris.org/fhir/StructureDefinition/registration-status",
          valueCoding: {
            system: "http://ihris.org/fhir/CodeSystem/registration-status",
            code: "pending",
            display: "Pending"
          }
        })
      }
      index = this.registration_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head-category"
      })
      if(this.school_fees_type.reference == "Basic/familyfive" || this.school_fees_type.reference == "Basic/familynine" || this.school_fees_type.reference == "Basic/familyinfinity") {
        if(index === -1) {
          this.registration_resource.extension.push({
            url: "http://ihris.org/fhir/StructureDefinition/family-head-category",
            valueCoding: this.family_head_category
          })
        } else {
          this.registration_resource.extension[index].valueCoding = this.family_head_category
        }
      } else if(index > -1) {
        this.registration_resource.extension.splice(index, 1)
      }
      let bundle = {
        entry: [{
          resource: this.registration_resource,
          request: {
            method: "PUT",
            url: "PractitionerRole/" + this.registration_resource.id
          }
        }]
      }
      if(this.family_head.reference) {
        let family_head_resource = {
          resourceType: "Basic",
          meta: {
            profile: [ "http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile" ]
          },
          extension: [{
            url: "http://ihris.org/fhir/StructureDefinition/family-head",
            valueReference: {
              reference: this.family_head.reference
            }
          }]
        }
        bundle.entry.push({
          resource: family_head_resource,
          request: {
            method: "POST",
            url: "Basic"
          }
        })
      }
      fetch(`/site/student/editRegistration/${this.queries.practitioner}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(bundle),
      }).then((response) => {
        this.overlay = false;
        if(response.ok) {
          this.$store.commit("setMessage", {
            type: "success",
            text: this.$t("Registration Edited Successfully"),
          });
          this.$router.push(`/custom/registration_view?path=/itsf&role=${this.queries.id}&practitioner=${this.queries.practitioner}`)
        } else {
          response.json().then((response) => {
            let text = "Failed to edit registration."
            if(response.message) {
              text = response.message
            }
            this.$store.commit("setMessage", {
              type: "error",
              text: text
            });
          }).catch(() => {
            this.$store.commit("setMessage", {
              type: "error",
              text: this.$t("Failed to edit registration")
            });
          })
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
      })
    },
    getRole() {
      this.overlay = true
      fetch("/fhir/PractitionerRole/" + this.queries.id).then(async(response) => {
        response.json().then(async (response) => {
          this.registration_resource = response
          this.unit = response.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-unit"
          })?.valueCoding
          this.stream = response.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-stream"
          })?.valueCoding
          this.accomodation = response.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-acommodation"
          })?.valueCoding
          this.student_type = response.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-student-type"
          })?.valueCoding
          this.school_fees_type = response.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-school-fees-type"
          })?.valueReference
          this.family_head_category = response.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head-category"
          })?.valueCoding
          let url = `/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile&practitioner=Practitioner/${this.queries.practitioner}&familymemberactive=yes`
          await fetch(url).then((response) => {
            response.json().then((response) => {
              if(response.entry && response.entry.length) {
                this.family_head_resource = response.entry[0].resource
                this.family_head = response.entry[0].resource.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head"
                })?.valueReference
              }
            })
          })
          this.overlay = false
        })
      })
    },
    getName() {
      this.$fhirutils.resourceLookup("Practitioner/" + this.queries.practitioner).then((response) => {
        this.fullname = response
      })
    },
    populateValues(children) {
      for (let child of children) {
        if(child.qField && child.path) {
          this[child.path] = child.value
        }
        if (child.$children) {
          this.populateValues(child.$children)
        }
      }
    }
  },
  components: {
    "fhir-choice": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-choice" ),
    "fhir-reference": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-reference" ),
  },
  created() {
    this.getRole()
    this.getName()
  },
}
</script>